import React from 'react';
import classes from './EachBlogDescDate.module.scss';
import dateimg from '../../img/date.png';

function EachBlogDescDate({ desc, date }) {
	return (
		<div dir="rtl" className="mr-3 w-50 h-50">
			<p style={{ color: 'grey', height: '65px' }} dir="rtl" className={`text-right ${classes.text}`}>
				{desc}
			</p>
			<p style={{ fontSize: '.9em' }} className="m-0 p-0 mt-1 text-right">
				<img alt="img" src={dateimg} className="pl-3" style={{ width: '40px' }} />
				{date}
			</p>
		</div>
	);
}

export default EachBlogDescDate;
