import React, { useState, useEffect } from 'react';
import queryString from 'query-string';
import { navigate } from 'gatsby';
import { Pagination, Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';

// Components
import Layout from '../components/Layout/Layout';
import EachBlogComp from '../components/EachBlogComp/EachBlogComp';
import EachBlogP from '../components/EachBlogP/EachBlogP';
import BlogsContainer from '../components/BlogsContainer/BlogsContainer';
import EachBlogDescDate from '../components/EachBlogDescDate/EachBlogDescDate';

const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
const contentful = require('contentful');
const client = contentful.createClient({
	space: `${process.env.SPACE_ID}`,
	accessToken: `${process.env.ACCESS_TOKEN}`
});

function Search(props) {
	const [ fetched, setFetched ] = useState([]);
	const [ total, setTotal ] = useState(1);
	const [ fetching, setFetch ] = useState(false);
	const parsed = queryString.parse(props.location.search);
	const lim = 6;
	let numnum = 1;
	if (parsed.page) {
		numnum = JSON.parse(parsed.page - 1);
	}
	const sk = numnum * lim;

	useEffect(
		() => {
			setFetch(true);
			client
				.getEntries({
					content_type: 'blog',
					'fields.title[match]': parsed.s,
					limit: lim,
					skip: sk,
					order: '-sys.createdAt'
				})
				.then((res) => {
					setFetched(res.items);
					setTotal(res.total);
					setFetch(false);
				})
				.catch((err) => {
					console.log(err.message);
					setFetch(false);
				});
		},
		[ props.location ]
	);

	return (
		<Layout title={`${parsed.s} جستجو`}>
			<Spin size="large" spinning={fetching} indicator={antIcon}>
				<BlogsContainer>
					{fetched.map((each) => {
						const title = each.fields.title.content[0].content[0].value;
						return (
							<EachBlogComp key={title} url={`/blog/${title}`}>
								<EachBlogP>{title}</EachBlogP>
								<div dir="rtl" className="d-flex">
									<img
										style={{ borderRadius: '5px', width: '160px' }}
										alt="img"
										src={each.fields.img.fields.file.url}
									/>
									<EachBlogDescDate
										desc={each.fields.desc.content[0].content[0].value}
										date={each.fields.date.content[0].content[0].value}
									/>
								</div>
							</EachBlogComp>
						);
					})}
				</BlogsContainer>
				{!fetched.length && !fetching ? (
					<h5 className="text-center mt-5" style={{ paddingBottom: '26.5em', color: '#9c9c9c' }}>
						مطلبی یافت نشد
					</h5>
				) : null}
			</Spin>
			{total > 5 ? (
				<div className="container d-flex justify-content-center mt-4">
					<Pagination
						current={JSON.parse(parsed.page)}
						pageSize={6}
						total={total}
						onChange={(num) => navigate(`/search?s=${parsed.s}&page=${num}`)}
						className="mt-3"
					/>
				</div>
			) : null}
		</Layout>
	);
}

export default Search;
