import React, { Fragment } from 'react';
import { navigate } from 'gatsby';
import classes from './EachBlogComp.module.scss';

function EachBlogComp({ children, url }) {
	return (
		<Fragment>
			<div
				role="button"
				className={`col-12 ${classes.container}`}
				onClick={() => navigate(url)}
				tabIndex={0}
				onKeyDown={() => navigate(url)}
			>
				{children}
				<hr className="m-0 p-0 mt-4 mb-2" style={{ background: '#E8E8E8' }} />
			</div>
		</Fragment>
	);
}

export default EachBlogComp;
